import React from "react"
// import { Styled } from "./ViewWord.styled"
import Search from "../../components/Search"
import { Link } from "gatsby"
import { DomainsHomeStyled } from "../Domains/DomainsHome.styled"

export default function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * Variables
   */
  // for content
  let { word_input } = that.props
  return (
    <DomainsHomeStyled>
      <Search
        {...that.props}
        className={!word_input ? "Search SearchHome" : "Search"}
        domains={false}
        title="Another word for..."
        placeholder="..."
        home={true}
        cue={[
          <span key="1">The most accurate thesaurus API&nbsp;</span>,
          <span key="2">for Natural Language Processing applications.</span>,
          <div key="3">
            Currently powers <Link to="/domain">NLP.domains</Link>. More info coming soon!
          </div>
        ]}
      />

      <div className="container">
        <div className="background">
          <article className="content">
            <h3>Try it out &#9757;</h3>
            <p>
              Type in any word, like <Link to="/word?str=aloha">aloha</Link> or{" "}
              <Link to="/word?str=appendectomy">appendectomy</Link>.
            </p>
            {/*<p>More coming soon! Scroll down to see examples...</p>*/}

            <h3 className="attention">The "best" thesaurus? Yes and no...</h3>

            <h4>For some applications, "yes": </h4>
            <p>
              <b>1) All English words are covered</b>, and short phrases.
              <br /> Other thesauruses will return results for only common words. Ours even includes prepositions,
              determiners, contractions, and other "auxillary" words which don't have their own meaning.
            </p>
            <p>
              <b>2) Most natural-sounding synonyms that a person might use</b>
              <br />
              Not "dictionary" style phrases and obscure words, but short conversational words. Our algorithms have
              found the shortest and most commonly used words, and placed them towards the front of the list. So, you
              can enhance your conversational AI, or just trust that our synonyms will be culturally accurate.
            </p>
            <p>
              <b>3) Sentiment analysis and cultural sensitivity built in</b>
              <br />
              We detect if a word might have negative connotation. You can choose to omit such words from your synonyms.
              The word "black" for example, has many negative synonyms like "evil" or "melancholy". We can omit these.
              For the most culturally sensitive or contradictory words, we've manually added many positive synonyms from
              every day language, instead of from a dictionary.
            </p>
            <p>
              <b>4) Word info</b>
              <br />
              Detect if the word is a Proper (Capitalized) name or place.{" "}
              <b>Most accurate "root word" extraction. Singular, plural, abbreviation, acronym</b>, auxillary word
              detection, etc...
            </p>
            <p>
              <b>5) Parts of speech</b>
              <br />
              Tell the primary, secondary part of speech, etc. Tell if it's a preposition, pronoun, determiner,
              exclamation, etc. For example, "light" is primarily a noun, then adjective, then verb. The word "they" is
              a pronoun, and has synonyms "he, she, we", etc.
            </p>

            <h4>And "no" for other applications: </h4>
            <p>
              We do <i>not</i> separate our synonyms into "senses". For each "part of speech", we return just one list
              of synonyms. This has the advantage of letting you know which synonyms overall are most important.
              However, a thesaurus with "senses" gives you different "meanings" of the word. It requires a lot more work
              for you to process, but the resulting analysis of the word can be more accurate.
            </p>
            <p>
              <b>For example, for the word "light",</b>
              <br />a thesaurus with "senses" like Oxford will return several lists of <i>adjectives</i>:
              <br />
              {/* prettier-ignore */}
              <code>
                [
                  ["bright","full of light","well lit","well lighted", ...],
                  ["light-coloured","light-toned","pale", ...],
                  ["easy to lift","not heavy","weighing very little","lightweight", ...],
                  ["flimsy","lightweight","insubstantial","thin", ...]
                ]
              </code>
              <br />
              We are planning to add this functionality soon. Just need to analyze all the synonyms of our synonyms, and
              group them together. Data science!
            </p>
            <p>
              Our thesaurus returns only a single list of words for <i>adjectives</i>:{/* prettier-ignore */}
              <code>
                [ablaze", "glowing", "airy", "shining", "easy", "buoyant", "illuminated", "casual", "sunny", "clean", "lightsome", "gleaming", "lit", "lighted", "fluorescent", "aglow", "...]
              </code>
            </p>
            <p>&nbsp;</p>
            <p>More info and tools coming soon... </p>
            <p>&nbsp;</p>
          </article>
        </div>
      </div>
    </DomainsHomeStyled>
  )
}
